import React from 'react';
import Widget from './Widget';

import './styles/Football.css';

class Football extends Widget {
  async getUpdateData() {
    return {
      name: 'football',
      title: 'Football fixtures',
      updateDelay: 1000*60*60,
      // fixtures: await this.fetchJson('/api/football')
      fixtures: [{"awayTeam":{"name":"Leicester","logo":"https://media.api-sports.io/football/teams/46.png"},"date":"2021-02-18T17:55:00+00:00","homeTeam":{"name":"Slavia Praha","logo":"https://media.api-sports.io/football/teams/560.png"},"league":{"name":"UEFA Europa League","logo":"https://media.api-sports.io/football/leagues/3.png"},"score":[0,0],"venue":"Sinobo Stadium"},{"awayTeam":{"name":"Leicester","logo":"https://media.api-sports.io/football/teams/46.png"},"date":"2021-02-21T14:05:00+00:00","homeTeam":{"name":"Aston Villa","logo":"https://media.api-sports.io/football/teams/66.png"},"league":{"name":"Premier League","logo":"https://media.api-sports.io/football/leagues/39.png"},"score":[null,null],"venue":"Villa Park"},{"awayTeam":{"name":"Slavia Praha","logo":"https://media.api-sports.io/football/teams/560.png"},"date":"2021-02-25T20:00:00+00:00","homeTeam":{"name":"Leicester","logo":"https://media.api-sports.io/football/teams/46.png"},"league":{"name":"UEFA Europa League","logo":"https://media.api-sports.io/football/leagues/3.png"},"score":[null,null],"venue":"King Power Stadium"}]
    };
  }
  renderInner() {
    if(this.state.fixtures) {
      return (<div className="Football-fixtures">{this.state.fixtures.map(f => React.createElement(Fixture, f))}</div>);
    }
  }
}

function Fixture({ awayTeam, date, homeTeam, league, score, venue }) {
  const d = new Date(date);
  return (
    <div className="Fixture">
      <div className="teams">
        <div className="home">
          <div className="logo"><img src={homeTeam.logo} alt="Home team logo" /></div>
          <div className="name">{homeTeam.name}</div>
        </div>
        <div className="info">
          <div className="inner">
            <div className="score">{score[0]} - {score[1]}</div>
            <div className="date">{d.getDate()}/{d.getMonth()+1}</div>
            <div className="venue">{venue}</div>
            <div className="league">{league.name}</div>
          </div>
        </div>
        <div className="away">
          <div className="logo"><img src={awayTeam.logo} alt="Away team logo" /></div>
          <div className="name">{awayTeam.name}</div>
        </div>
      </div>
    </div>
  );
}

export default Football;
